@import "../../../../design-system/src/styles.scss";

:host {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  font-family: $ds-font-family;
  background: $ds-light;
}

.ds-welcome-hero {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  background: linear-gradient(90deg, $ds-white 70%, $ds-primary 120%);
  border-radius: $ds-spacing-xl;
  box-shadow: 0 8px 32px rgba($ds-dark, 0.09), 0 2px 6px rgba($ds-primary, 0.1);
  width: 100%;
  max-width: 960px;
  min-height: 340px;
  overflow: hidden;
}

.ds-welcome-hero__left {
  flex: 1 1 0;
  padding: $ds-spacing-xl $ds-spacing-xl $ds-spacing-xl $ds-spacing-xl;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 320px;
}

.ds-welcome-logo {
  margin-bottom: $ds-spacing-lg;
  filter: drop-shadow(0 3px 12px rgba($ds-primary, 0.21));
}

.ds-welcome-slogan {
  color: $ds-primary;
  font-size: $ds-font-size-xl;
  font-weight: $ds-font-weight-bold;
  margin-bottom: $ds-spacing-md;
  line-height: 1.1;
  letter-spacing: 0.5px;
  font-family: $ds-font-family;
  text-shadow: 0 1px 10px rgba($ds-primary, 0.07);
}

.ds-welcome-desc {
  color: $ds-dark;
  font-size: $ds-font-size-lg;
  font-weight: $ds-font-weight-normal;
  font-family: $ds-font-family;
  margin-bottom: 0;
  line-height: 1.5;
  text-shadow: 0 1px 6px rgba($ds-gray, 0.11);
}

.ds-welcome-hero__right {
  display: flex;
  align-items: center;
  justify-content: center;
  background: $ds-primary;
  flex: 0 0 320px;
  min-width: 220px;
  position: relative;
}

.ds-welcome-feito {
  color: $ds-white;
  font-size: 5.5rem;
  font-weight: $ds-font-weight-bold;
  font-family: $ds-font-family;
  letter-spacing: 3px;
  text-shadow: 0 6px 32px rgba($ds-dark, 0.2), 0 2px 8px rgba($ds-white, 0.13);
  margin-right: -$ds-spacing-lg;
  margin-left: $ds-spacing-lg;
  user-select: none;
}

@media (max-width: 900px) {
  .ds-welcome-hero {
    flex-direction: column;
    min-height: 0;
    max-width: 98vw;
  }
  .ds-welcome-hero__right {
    min-width: 0;
    flex: none;
    width: 100%;
    justify-content: flex-end;
    padding: $ds-spacing-lg;
    background: linear-gradient(90deg, $ds-primary 70%, $ds-white 100%);
  }
  .ds-welcome-feito {
    font-size: 3.5rem;
    margin-right: 0;
    margin-left: 0;
  }
  .ds-welcome-hero__left {
    padding: $ds-spacing-xl $ds-spacing-lg $ds-spacing-lg $ds-spacing-xl;
    min-width: 0;
  }
}

@media (max-width: 500px) {
  .ds-welcome-hero__left {
    padding: $ds-spacing-lg;
  }
  .ds-welcome-feito {
    font-size: 2.2rem;
  }
}

.ds-welcome-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  background: $ds-gray;
  border-radius: 32px;
  width: 180px;
  height: 180px;
  margin: 40px auto;
}

.ds-welcome-smile {
  font-family: $ds-font-family;
  font-size: 80px;
  font-weight: 700;
  color: $ds-primary;
  margin: 0;
  line-height: 1;
  user-select: none;
}
